import React, { useEffect, useState } from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Grid, Typography, Container, useMediaQuery } from '@mui/material';

import { Route, Routes, BrowserRouter, useLocation } from 'react-router-dom';

import Sidebar from './components/Sidebar';
import About from './routes/About';
import Work from './routes/Work';
import Contact from './routes/Contact';
import Experience from './routes/Experience';
import './App.css';

function App() {
  const [activeTab, setActiveTab] = useState('/');

  const handleTabChange = (event, newValue) => {
    {/*setActiveTab(newValue);*/}
  };

  const darkTheme = createTheme({
    palette: {
      mode: 'dark'
    }
  });
  
  const isMobile = useMediaQuery( darkTheme.breakpoints.down( 'md' ) );

  {/*function Tabs() {
    return(
      <div>
        {activeTab === 0 && <About theme={darkTheme} />}
        {activeTab === 1 && <Work theme={darkTheme} />}
        {activeTab === 2 && <Experience theme={darkTheme} />}
        {activeTab === 3 && <Contact theme={darkTheme} />}
      </div>
    );
  }*/}

  function LocationIdentifier() {
    const { pathname } = useLocation();

    useEffect(() => {
      setActiveTab( pathname );
    }, [pathname]);

    return null;
  }

  return (
    <BrowserRouter>
    <LocationIdentifier />
    <ThemeProvider theme={darkTheme}>
      <div style={{ 
        backgroundColor: `${darkTheme.palette.background.default+'44'}`, 
        height: isMobile ? 'auto' : '100vh', 
        display: 'flex', 
        alignItems: 'center', 
        justifyContent: 'center'
      }} >
        <Container className='main-content' style={{ display: 'flex', transition: '.3s ease' }}>
          <Grid container spacing={2} style={{ flex: 1 }}>
            <Grid item xs={12} md={3}>
              <Sidebar theme={darkTheme} activeTab={activeTab} handleTabChange={handleTabChange}/>
            </Grid>
            <Grid item xs={12} md={9}>
              <div style={{ padding: 16 }}>
                <Routes>
                  <Route path="/" element={<About/>} />
                  <Route path="/work" element={<Work/>} />
                  <Route path="/experience" element={<Experience/>} />
                  <Route path="/contact" element={<Contact/>} />
                </Routes>
              </div>
            </Grid>
          </Grid>
        </Container>
      </div>
    </ThemeProvider>
    </BrowserRouter>
    
  );
}

export default App;
